.booksContainer {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, 222px);
  justify-content: center;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

.bookCard {
  display: block;
  width: 200px;
  text-align: center;
  padding: 10px;
  border: 1px rgba(0, 0, 0, 0.301) solid;
  cursor: pointer;
}

@media (hover: hover) {
  .bookCard:hover {
    box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
  }
}

@media (hover: none) {
  .bookCard:active {
    box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
  }
}

.bookImage {
  width: 150px;
}

@media screen and (max-width: 605px) {
  .bookText {
    font-size: 1.2rem;
  }
}
